import React from "react";
import { Row, Column } from "components/Grid";
import Select from "react-select";
import { navigate } from "gatsby";
import colors from "styles/colors";

import {
  TitleWrapper,
  ContentWrapper,
  Content,
  Container,
  Learning,
  StyledLink,
  LetterWrapper,
  SelectWrapper,
  LearningCenterLink,
} from "./elements";

interface Props {
  gutterBackground: string;
  location: Location;
  glossary: Array<{
    fields: {
      glossaryTitle: string;
    };
  }>;
}

const customStyles = {
  option: (provided: Object, state: { isDisabled: boolean }) => ({
    ...provided,
    color: state.isDisabled ? colors.gray1 : colors.black,
  }),
};

const letters = "ABCDEFGHIJKLMNOPQRSTUVWXYZ".split("");

/* eslint-disable-next-line no-unused-vars */
const SimpleHero = ({ gutterBackground, location, glossary }: Props) => {
  const letterFromUrl = location.pathname
    .split("/")
    .filter(Boolean)
    .pop();
  return (
    <Container>
      <Row padding={30} gutterWidth={26}>
        <Column span={1} spanMedium={1} spanSmall={0} />
        <Column span={10} spanMedium={10} spanSmall={12}>
          <ContentWrapper>
            <Content>
              <LearningCenterLink to="/learn">
                <Learning>Brex Learning Center</Learning>
              </LearningCenterLink>
              <TitleWrapper>Glossary</TitleWrapper>
            </Content>
          </ContentWrapper>
          <LetterWrapper>
            {letters.map((letter) => {
              const filteredNames = glossary.filter(
                (filter: { fields: { glossaryTitle: string } }) =>
                  filter.fields.glossaryTitle.charAt(0) ===
                  letter.toUpperCase(),
              );
              return (
                <StyledLink
                  key={letter}
                  existed={filteredNames.length > 0}
                  selected={letterFromUrl === letter.toLowerCase()}
                  to={`/learn/glossary/${letter.toLowerCase()}`}
                >
                  {letter}
                </StyledLink>
              );
            })}
          </LetterWrapper>
          <SelectWrapper>
            <Select
              styles={customStyles}
              value={{
                value: letterFromUrl && letterFromUrl.toUpperCase(),
                label: letterFromUrl && letterFromUrl.toUpperCase(),
              }}
              onChange={(selected: { value: string }) => {
                navigate(`/learn/glossary/${selected.value.toLowerCase()}`);
              }}
              options={letters.map((letter) => {
                const filteredNames = glossary.filter(
                  (filter: { fields: { glossaryTitle: string } }) =>
                    filter.fields.glossaryTitle.charAt(0) ===
                    letter.toUpperCase(),
                );

                return {
                  label: letter,
                  value: letter,
                  isDisabled: filteredNames.length === 0,
                };
              })}
            />
          </SelectWrapper>
        </Column>
        <Column span={1} spanMedium={1} spanSmall={0} />
      </Row>
    </Container>
  );
};

export default SimpleHero;
