import React from "react";
import ReactMarkdown from "react-markdown";
import arrow from "assets/img/icons/orange/link-arrow.svg";
import {
  Wrapper,
  TitleWrapper,
  Paragraph,
  WrapperSelected,
  TitleWrapperLink,
  LinkArrow,
} from "./elements";

interface Props {
  title: String;
  paragraph: string;
  url: string | null;
  key: string;
}

const SectionWrapper = ({ title, paragraph, url = null }: Props) => {
  if (url === null) {
    return (
      <Wrapper>
        <TitleWrapper>{title}</TitleWrapper>
        <Paragraph>
          <ReactMarkdown source={paragraph} />
        </Paragraph>
      </Wrapper>
    );
  }

  return (
    <WrapperSelected>
      <LinkArrow>
        <img src={arrow} alt="arrow" />
      </LinkArrow>
      <TitleWrapperLink to={`/${url}`}>
        <TitleWrapper>{title}</TitleWrapper>
      </TitleWrapperLink>
      <Paragraph>
        <ReactMarkdown source={paragraph} />
      </Paragraph>
    </WrapperSelected>
  );
};

export default SectionWrapper;
