import React from "react";

import SectionWrapper from "components/learningCenter/glossary/section";
import { Column, Row } from "components/Grid";
import { Section, FooterContainer, Letter } from "./elements";

interface Props {
  location: Location;
  glossary: Array<{
    fields: {
      glossaryTitle: string;
      glossaryDescription: string;
      url: string | null;
    };
  }>;
}

const Content = ({ glossary, location }: Props) => (
  <Section>
    <Row padding={30} gutterWidth={26}>
      <Column span={1} spanMedium={1} spanSmall={0} />
      <Column span={10} spanMedium={10} spanSmall={12}>
        <Letter>
          {location.pathname
            .split("/")
            .filter(Boolean)
            .pop()}
        </Letter>
        <FooterContainer>
          {glossary
            .sort((a, b) =>
              a.fields.glossaryTitle.localeCompare(b.fields.glossaryTitle),
            )
            .map((glossaryItem) => (
              <SectionWrapper
                key={glossaryItem.fields.glossaryTitle}
                title={glossaryItem.fields.glossaryTitle}
                paragraph={glossaryItem.fields.glossaryDescription}
                url={glossaryItem.fields.url}
              />
            ))}
        </FooterContainer>
      </Column>
      <Column span={1} spanMedium={1} spanSmall={0} />
    </Row>
  </Section>
);

export default Content;
